<template>
  <div>
    <v-data-table
      :headers="headers"
      :items="dateFormat"
      hide-actions
    >
      <template
        slot="items"
        slot-scope="{ item }"
      >
        <td class="hidden-text">
          {{ item.comment }}
        </td>
        <td>{{ item.rating }}점</td>
        <td class="customFontSize">
          {{ item.date }}
        </td>
        <td class="customFontSize hidden-text">
          {{ item.writer }}
        </td>
      </template>
    </v-data-table>
  </div>
</template>
<script>
export default {
  props: {
    items: {
      type: Array,
      default: null
    },
    linkBtn: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      headers: [
        {
          sortable: false,
          text: '커맨트',
          width: '140'
        },
        {
          sortable: false,
          width: '40'
        },
        {
          sortable: false,
          text: '작성일',
          width: '60'
        },
        {
          sortable: false,
          text: '작성자',
          width: '60'
        }
      ]
    }
  },
  computed: {
    dateFormat () {
      return this.items.map((item) => {
        item.date = this.$moment(item.date).format('YYYY-MM-DD')
        return item
      })
    }
  }
}
</script>
<style>
.customFontSize {
  font-size: 1em!important;
}
<style>
.hidden-text {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
table {
  table-layout: fixed;
}
</style>
