<template>
  <div>
    <v-data-table
      :headers="headers"
      :items="items"
      hide-actions
    >
      <template
        slot="items"
        slot-scope="{ item }"
      >
        <tr
          @mouseover="enableShowInfoWindow(item)"
          @mouseout="disableShowInfoWindow()"
        >
          <td class="hidden-text">
            {{ item.name }}
          </td>
          <td class="align-center">
            {{ item.rating }}
          </td>
          <td class="align-center">
            {{ item.review_amount }}
          </td>
          <td
            v-if="linkBtn"
            class="text-xs-right"
          >
            <v-btn
              slot="activator"
              class="v-btn--simple"
              color="success"
              icon
              @click="moveRestaurantInfo(item)"
            >
              <v-icon color="primary">
                mdi-arrow-right-bold
              </v-icon>
            </v-btn>
          </td>
        </tr>
      </template>
    </v-data-table>
  </div>
</template>
<script>
export default {
  props: {
    items: {
      type: Array,
      default: null
    },
    linkBtn: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      headers: [
        {
          sortable: false,
          text: '가게이름',
          value: 'Name',
          width: '170'
        },
        {
          sortable: false,
          text: '평점',
          value: 'Star',
          width: '40',
          align: 'center'
        },
        {
          sortable: false,
          text: '갯수',
          value: 'Review',
          width: '40',
          align: 'center'
        },
        {
          sortable: false,
          width: '40',
          align: 'center'
        }
      ]
    }
  },
  created () {
  },
  methods: {
    moveRestaurantInfo (restaurant) {
      this.$router.push({ name: 'RestaurantInfo', params: { id: restaurant.restaurant_id, restaurantInfo: restaurant } })
    },
    enableShowInfoWindow (restaurant) {
      this.$emit('enableShowInfoWindow', restaurant)
    },
    disableShowInfoWindow () {
      this.$emit('disableShowInfoWindow', null)
    }
  }
}
</script>
<style>
.hidden-text {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
table {
  table-layout: fixed;
}
.align-center {
  text-align: center;
}
</style>
